<template>
    <div class="music-list">
        <div class="back" @click="back">
            <i class="icon-back"></i>
        </div>
        <h1 class="title" v-html="title"></h1>
        <div class="bg-image" :style="bgStyle" ref="bgImageStyle">
            <div class="play-wrapper" v-show="songs.length>0" ref="playBtn">
                <div class="play" @click="random">
                    <i class="icon-play"></i>
                    <span class="text">随机播放全部</span>
                </div>
            </div>
            <div class="filter" ref="filter"></div>
        </div>
        <div class="bg-layer" ref="bgLayer"></div>
        <MScroll :data="songs"
                 class="list"
                 @scroll="scroll"
                 :probeType="probeType"
                 :listenScroll="listenScroll"
                 ref="list">
            <div class="song-list-wrapper">
                <MSongList :rank="rank" @select="selectItem" :songs="songs"></MSongList>
            </div>
            <div class="loading-container" v-show="!songs.length">
                <MLoading></MLoading>
            </div>
        </MScroll>
    </div>
</template>

<script>
    import MScroll from '@/components/base/Scroll'
    import MSongList from '@/components/base/SongList'
    import MLoading from '@/components/base/Loading'
    import {prefixStyle} from 'js/dom'
    import {mapActions} from 'vuex'
    import {playlistMixin} from 'js/mixin'
    const RESERVED_HEIGHT = 40
    const transform=prefixStyle('transform');
    const backdropFilter=prefixStyle('backdrop-filter');
    export default {
        mixins:[playlistMixin],
        name: "MusicList",
        components:{
            MScroll,
            MSongList,
            MLoading
        },
        props:{
            bgImage:{
                type:String,
                default:''
            },
            songs:{
                type:Array,
                default:[]
            },
            title:{
                type:String,
                default:''
            },
            rank:{
                type:Boolean,
                default:false
            }
        },
        data(){
            return {
                scrollY:0
            }
        },
        created(){
            this.probeType=3;
            this.listenScroll=true
        },
        computed:{
            bgStyle(){
                return `background-image:url(${this.bgImage})`
            }
        },
        watch:{
            scrollY(newY){
                let zIndex=0;
                let scale = 1;
                let blur=0;
                this.translateY=Math.max(this.minTranslateY,newY);
                this.$refs.bgLayer.style[transform]=`translate3d(0,${this.translateY}px,0)`;
                // 滚动覆盖图片
                if(newY<this.minTranslateY){
                    zIndex=10;
                    this.$refs.bgImageStyle.style.paddingTop = 0;
                    this.$refs.bgImageStyle.style.height = `${RESERVED_HEIGHT}px`;
                    this.$refs.playBtn.style.display='none'
                }else{
                    this.$refs.bgImageStyle.style.paddingTop = "70%";
                    this.$refs.bgImageStyle.style.height =0;
                    this.$refs.playBtn.style.display=''
                }
                const percent=Math.abs(newY/this.bgImageHeight);
                // 图片伸缩
                if(newY>0){
                    scale=percent+1;
                    zIndex=10;
                }else{
                    blur=Math.min(20*percent,20)
                }
                this.$refs.bgImageStyle.style[transform]=`scale(${scale})`;
                this.$refs.filter.style[backdropFilter]=`blur(${blur}px)`;
                this.$refs.bgImageStyle.style.zIndex=zIndex
            }
        },
        methods:{
            onHandlePlaylist(playlist){
                const bottom=playlist.length>0?'60px':'';
                this.$refs.list.$el.style.bottom=bottom;
                this.$refs.list.refresh();
            },
            scroll(pos){
                this.scrollY=pos.y;
            },
            back(){
                this.$router.back()
            },
            selectItem(song,index){
                this.selectPlay({
                    list:this.songs,
                    index:index
                })
            },
            // 随机播放
            random(){
                this.randomPlay({list:this.songs})
            },
            ...mapActions([
                'selectPlay',
                'randomPlay'
            ])
        },
        mounted(){
            this.bgImageHeight=this.$refs.bgImageStyle.clientHeight;
            this.minTranslateY=-this.bgImageHeight+RESERVED_HEIGHT
            this.$refs.list.$el.style.top=`${this.bgImageHeight}px`
        }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~stylus/variable"
    @import "~stylus/mixin"

    .music-list
        position: fixed
        z-index: 100
        top: 0
        left: 0
        bottom: 0
        right: 0
        background: $color-background
        .back
            position absolute
            top: 0
            left: 6px
            z-index: 500
            .icon-back
                display: block
                padding: 10px
                font-size: $font-size-large-x
                color: $color-theme
        .title
            position: absolute
            top: 0
            left: 10%
            z-index: 40
            width: 80%
            no-wrap()
            text-align: center
            line-height: 40px
            font-size: $font-size-large
            color: $color-text
        .bg-image
            position: relative
            width: 100%
            height: 0
            padding-top: 70%
            transform-origin: top
            background-size: cover
            .play-wrapper
                position: absolute
                bottom: 20px
                z-index: 50
                width: 100%
                .play
                    box-sizing: border-box
                    width: 135px
                    padding: 7px 0
                    margin: 0 auto
                    text-align: center
                    border: 1px solid $color-theme
                    color: $color-theme
                    border-radius: 100px
                    font-size: 0
                    .icon-play
                        display: inline-block
                        vertical-align: middle
                        margin-right: 6px
                        font-size: $font-size-medium-x
                    .text
                        display: inline-block
                        vertical-align: middle
                        font-size: $font-size-small
            .filter
                position: absolute
                top: 0
                left: 0
                width: 100%
                height: 100%
                background: rgba(7, 17, 27, 0.4)
        .bg-layer
            position: relative
            height: 100%
            background: $color-background
        .list
            position: absolute
            top: 0
            bottom: 0
            width: 100%
            background: $color-background
            .song-list-wrapper
                padding: 20px 30px
            .loading-container
                position: absolute
                width: 100%
                top: 50%
                transform: translateY(-50%)
</style>


